.box {
    border: 1px solid $light-gray;
    box-shadow: none;
    border-radius: 0;
    &.box-primary {
        border-top-color: transparent;
    }
}

.box-body {
    padding: 20px;
}

.box-footer {
    border-top-color: transparent;
    padding: 1.375em 2.375em;
}

.box-footer .form-inline {
    .btn-group.open {
        position: relative;
    }
}
.mfp-content .sonata-ba-delete {
    margin: auto;
    max-width: 355px;
    position: relative;
}
.sonata-ba-delete {

    .box-danger {
        border-top-color: transparent;
        padding-bottom: 1.75em;
        padding-top: 1.75em;
        .box-header {
            display: block;
            padding: 0.75em 2.375em;
        }
        .box-title {
            font-size: 24px;
        }
        .box-body {
            padding: 0 2.375em 0.75em;
        }
        .box-footer {
            padding: 0 2.375em 0.75em;
        }
        form {
            font-size: 0;
            margin: 0 -2em;
        }
        .btn {
            margin: 0.5em 1em 0 0;
        }
    }
}

.sonata-ba-delete.popup-file-delete {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    * {
        transition: opacity 0.5s;
    }
    .box.is_active * {
        opacity: 0.5;
        pointer-events: none;
    }
}

.delete-dialog {
    color: #d73925;
    display: none;
    padding: 0.75rem 1rem;
    &.is-active {
        display: block;
    }
    .delete-dialog__cancel-btn {
        color: #000;
        text-decoration: underline;
    }
}

.tab-pane .box {
    border: 0;
}

.sonata-ba-form {
    .box {
        margin: 0;
        background-color: $lighter-gray;
    }
    .box-body {
        margin: 0;
        padding: 0;
    }
}

.box-header {
    display: none;
}

.sonata-filters-box {
    .box {
        &.box-primary {
            border-top: 0;
        }
    }
}

.info-box {
    min-height: 0;
    margin: 0;
    box-shadow: none;
}

.info-box-icon {
    display: none;
}

.info-box-content {
    margin-left: 0;
    padding: 1.2em 1.5em;
}

.info-box .progress {
    display: none;
}

.info-box-text {
    font-size: 1.2em;
    border-bottom: 1px solid $gray;
    padding: 0 0 0.4em 0;
}

.progress-description .sonata-action-element {
    display: inline-block;
    padding: .8em 1.1em;
    margin: 1em 0 0;
    background-color: $primary-color;
    color: #FFF;
    &:hover {
        background-color: lighten($primary-color, 10%);
    }
}

.box-footer .form-inline {
    .pull-left {
        opacity: 1;
    }
}



