table {
    border-collapse: separate;
}

.table-bordered {
    border: 0;
}

table.sonata-ba-list th {
    background-image: none;
}

table.sonata-ba-list td {
    overflow: initial;
}

.box-body.table-responsive {
    overflow-x: scroll;
}

.table>tbody>tr>td,
.table>tbody>tr>th,
.table>tfoot>tr>td,
.table>tfoot>tr>th,
.table>thead>tr>td,
.table>thead>tr>th {
    vertical-align: top;
}

.delete_link {
    max-width: 58px;
}

.table>thead>.sonata-ba-list-field-header>th {
    vertical-align: top;
}

.sonata-ba-list tr td:last-child {
    position: relative;
    overflow: visible;
    margin: 0;
    padding: 0;
    padding-right: 2.375em;
    text-align: right;
    vertical-align: middle;
    .btn-group {
        .btn:last-child {
            margin-left: 1em;
        }
    }
}

.table-bordered>thead>tr>th:last-child {
    padding-right: 2.375em;
    text-align: right;
}

.sonata-ba-list td.sonata-ba-list-field a.sonata-link-identifier {
    color: #000;
    font-weight: normal;
    &:hover,
    &:focus,
    &:active {
        color: #888;
    }
}

.sonata-ba-list-field-header-text:last-child {
    display: none;
}

table.sonata-ba-list tr td a {
    color: #888;
    &:hover {
        color: #000;
    }
}

th.sonata-ba-list-field-header-order-desc a,
th.sonata-ba-list-field-header-order-asc a {
    color: #000;
}

.table-bordered>thead>tr>th,
.table-bordered>tbody>tr>th,
.table-bordered>tfoot>tr>th,
.table-bordered>thead>tr>td,
.table-bordered>tbody>tr>td,
.table-bordered>tfoot>tr>td {
    border: 1px solid transparent;
    padding: 1.35em 0.5em;
    &:first-child {
        padding-left: 2.5em;
    }
}

.table-bordered>thead>tr>.sonata-ba-list-field-header-batch,
.table-bordered>tbody>tr>.sonata-ba-list-field-batch {
    padding-left: 0.5em !important;
    width: 2em;
    input {
        position: absolute;
        left: 0.9em;
    }
}

td.sonata-ba-list-field.sonata-ba-list-field-currency,
td.sonata-ba-list-field.sonata-ba-list-field-percent,
td.sonata-ba-list-field.sonata-ba-list-field-integer {
    text-align: left;
}

.table-striped>tbody>tr:nth-of-type(odd) {
    background-color: $lighter-gray;
    &:hover, &:focus, &:active {
        background-color: $gray;
    }
}
.just-records-list--tabs {
    .table-striped {
        // background-color: $lighter-gray;
        padding-top: 2em;
    }
    .table-striped>tbody>tr:nth-of-type(odd) {
        // background-color: #fff;
        // &:hover, &:focus, &:active {
        //     background-color: $gray;
        // }
    }
}

.sonata-ba-list tr {
    cursor: pointer;
    transition: background-color 0.1s;
    &:hover, &:focus, &:active {
        background-color: $gray;
    }
}

.sonata-ba-list thead tr {
    cursor: default;
     &:hover, &:focus, &:active {
        background-color: transparent;
    }
}

.table-striped tbody tr.sonata-ba-list-row-selected td,
.table-striped tbody tr.sonata-ba-list-row-selected th {
    background-color: $gray;
    border-color: $gray;
}

.label {
    font-size: 100%;
    border-radius: 0;
}

@media screen and (min-width: 50em) {
    .box-body.table-responsive {
        overflow: visible;
        overflow-x: visible;
    }
}



