.alert {
	border-radius: 0;
	font-weight: bold;
}

.alert .close {
	text-indent: -999em;
	opacity: 1;
}

.alert-dismissable {
	animation: alertShow 0.5s 0.35s forwards, alertHide 0.5s 3s forwards;
	position: fixed;
	bottom: 0;
	left: 0;
	margin: 0;
	transform: translateY(100%);
	right: 0;
	z-index: 10;
}
.alert-dismissable.no-delay {
	animation: alertShow 0.5s 0s forwards, alertHide 0.5s 3s forwards;
}

@keyframes alertShow {
  0%   { transform: translateY(100%); }
  100% { transform: translateY(0); }
}

@keyframes alertHide {
	0%   { transform: translateY(0); }
	100%   { transform: translateY(100%); }
}

@media screen and (min-width: 768px) {
	.alert-dismissable {
		left: 230px;
	}
}

@media screen and (min-width: 95em) {
	.alert-dismissable {
		left: 20%;
	}
}

.bg-green,
.callout.callout-success,
.alert-success,
.label-success,
.modal-success .modal-body {
	background-color: $success !important;
}

.alert-success {
	border-color: $success !important;
}

.bg-red,
.callout.callout-danger,
.alert-danger,
.alert-error,
.label-danger,
.modal-danger .modal-body {
    background-color: $danger !important;
}

.has-error .checkbox,
.has-error .checkbox-inline,
.has-error .control-label,
.has-error .help-block,
.has-error .radio,
.has-error .radio-inline,
.has-error.checkbox label,
.has-error.checkbox-inline label,
.has-error.radio label,
.has-error.radio-inline label,
.form-group.has-error label {
	color: $danger !important;
}

.form-group.has-error .form-control,
.form-group.has-error .input-group-addon {
	border-color: $danger !important;
}
.sonata-ba-list-field-boolean .label {
	margin-left: -0.5em;
	padding: 0.5em;
}
.sonata-ba-list-field-boolean .label-success,
.sonata-ba-list-field-boolean .label-danger {
	background-color: transparent !important;
	color: $success !important;
	transition: 0.1s;
}
.sonata-ba-list-field-boolean .label-success {
	color: $success !important;
}
.sonata-ba-list-field-boolean .label-danger {
	color: $danger !important;
}
.sonata-ba-list-field-boolean a.label-success:hover,
.sonata-ba-list-field-boolean a.label-danger:hover  {
	background-color: #999 !important;
	color: #fff !important;
}


